"use client";

import React from 'react';
import { MapPin, Mail, Phone } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { useForm, SubmitHandler } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { useToast } from '@/hooks/use-toast';


const formSchema = z.object({
  name: z.string().min(2, { message: "Please enter at least 2 characters." }),
  email: z.string().email({ message: "Please enter a valid email address." }),
  subject: z.string().min(4, { message: "Please enter at least 4 characters." }),
  message: z.string().min(10, { message: "Please enter at least 10 characters." }),
});

type FormData = z.infer<typeof formSchema>;


const ContactSection: React.FC = () => {
    const { toast } = useToast();
    const form = useForm<FormData>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            name: "",
            email: "",
            subject: "",
            message: "",
        },
    });

    const onSubmit: SubmitHandler<FormData> = async (data) => {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));
        console.log(data);
        toast({
             title: "Message Sent!",
             description: "Thank you for contacting us. We will get back to you shortly.",
        });
        form.reset(); // Reset form after successful submission
    };

  const contactInfo = [
    {
      icon: <MapPin className="h-6 w-6 text-accent flex-shrink-0" />,
      title: 'Our Address',
      lines: ['A108 Adam Street, New York, NY 535022'],
    },
    {
      icon: <Mail className="h-6 w-6 text-accent flex-shrink-0" />,
      title: 'Email Us',
      lines: ['info@example.com', 'contact@example.com'],
      isLink: true,
      hrefPrefix: 'mailto:',
    },
    {
      icon: <Phone className="h-6 w-6 text-accent flex-shrink-0" />,
      title: 'Call Us',
      lines: ['+1 5589 55488 55', '+1 6678 254445 41'],
       isLink: true,
       hrefPrefix: 'tel:',
    },
  ];

  return (
    <section id="contact" className="bg-background">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-primary mb-2">Contact Us</h2>
          <div className="inline-block w-16 h-1 bg-accent mb-4"></div>
           <p className="text-muted-foreground max-w-3xl mx-auto">
             Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.
           </p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Contact Info */}
          <div className="lg:col-span-1 space-y-6">
             {contactInfo.map((info, index) => (
               <div key={index} className="flex items-start space-x-4">
                 <div className="mt-1 p-3 rounded-full bg-accent/10 inline-flex items-center justify-center">
                     {info.icon}
                 </div>
                 <div>
                   <h3 className="text-lg font-semibold text-primary mb-1">{info.title}</h3>
                   {info.lines.map((line, lineIndex) => (
                       info.isLink ? (
                           <a key={lineIndex} href={`${info.hrefPrefix}${line}`} className="block text-muted-foreground text-sm hover:text-accent transition-colors duration-200">{line}</a>
                       ) : (
                            <p key={lineIndex} className="text-muted-foreground text-sm">{line}</p>
                       )
                   ))}
                 </div>
               </div>
             ))}
           </div>

          {/* Contact Form */}
          <div className="lg:col-span-2">
            <Form {...form}>
                 <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6 bg-white p-8 rounded-lg shadow-lg border border-border">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                         <FormField
                           control={form.control}
                           name="name"
                           render={({ field }) => (
                             <FormItem>
                               <FormLabel>Your Name</FormLabel>
                               <FormControl>
                                 <Input placeholder="John Doe" {...field} className="focus:ring-accent focus:border-accent" />
                               </FormControl>
                               <FormMessage />
                             </FormItem>
                           )}
                         />
                         <FormField
                           control={form.control}
                           name="email"
                           render={({ field }) => (
                             <FormItem>
                               <FormLabel>Your Email</FormLabel>
                               <FormControl>
                                 <Input type="email" placeholder="john.doe@example.com" {...field} className="focus:ring-accent focus:border-accent" />
                               </FormControl>
                               <FormMessage />
                             </FormItem>
                           )}
                         />
                     </div>
                     <FormField
                       control={form.control}
                       name="subject"
                       render={({ field }) => (
                         <FormItem>
                           <FormLabel>Subject</FormLabel>
                           <FormControl>
                             <Input placeholder="Regarding your services" {...field} className="focus:ring-accent focus:border-accent" />
                           </FormControl>
                           <FormMessage />
                         </FormItem>
                       )}
                     />
                     <FormField
                       control={form.control}
                       name="message"
                       render={({ field }) => (
                         <FormItem>
                           <FormLabel>Message</FormLabel>
                           <FormControl>
                             <Textarea placeholder="Your message here..." {...field} rows={5} className="focus:ring-accent focus:border-accent" />
                           </FormControl>
                           <FormMessage />
                         </FormItem>
                       )}
                     />
                     <div className="text-center">
                         <Button type="submit" disabled={form.formState.isSubmitting} className="bg-accent text-accent-foreground hover:bg-accent-dark transition-colors duration-300 px-6 py-2.5">
                            {form.formState.isSubmitting ? 'Sending...' : 'Send Message'}
                         </Button>
                     </div>
                 </form>
             </Form>
           </div>
        </div>

        {/* Map Placeholder */}
        <div className="mt-12 h-80 bg-secondary rounded-lg overflow-hidden flex items-center justify-center text-muted-foreground">
            {/* In a real app, replace this with an embedded map component (e.g., Google Maps, Leaflet) */}
            <p>Map Placeholder</p>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;
