"use client";

import React, { useState } from 'react';
import { Button } from '@/components/ui/button';
import Image from 'next/image';
import { Card, CardContent } from '@/components/ui/card';
import { Plus, Link as LinkIcon } from 'lucide-react';
import Link from 'next/link';

type Category = 'all' | 'app' | 'card' | 'web';

interface PortfolioItem {
  id: number;
  category: Category;
  title: string;
  imageUrl: string;
}

const portfolioItems: PortfolioItem[] = [
  { id: 1, category: 'app', title: 'App 1', imageUrl: 'https://picsum.photos/seed/p1/600/400' },
  { id: 2, category: 'web', title: 'Web 3', imageUrl: 'https://picsum.photos/seed/p2/600/400' },
  { id: 3, category: 'app', title: 'App 2', imageUrl: 'https://picsum.photos/seed/p3/600/400' },
  { id: 4, category: 'card', title: 'Card 2', imageUrl: 'https://picsum.photos/seed/p4/600/400' },
  { id: 5, category: 'web', title: 'Web 2', imageUrl: 'https://picsum.photos/seed/p5/600/400' },
  { id: 6, category: 'app', title: 'App 3', imageUrl: 'https://picsum.photos/seed/p6/600/400' },
  { id: 7, category: 'card', title: 'Card 1', imageUrl: 'https://picsum.photos/seed/p7/600/400' },
  { id: 8, category: 'card', title: 'Card 3', imageUrl: 'https://picsum.photos/seed/p8/600/400' },
  { id: 9, category: 'web', title: 'Web 3', imageUrl: 'https://picsum.photos/seed/p9/600/400' }, // Note: Renva has duplicate Web 3, replicating here
];

const PortfolioSection: React.FC = () => {
  const [activeFilter, setActiveFilter] = useState<Category>('all');

  const filteredItems = activeFilter === 'all'
    ? portfolioItems
    : portfolioItems.filter(item => item.category === activeFilter);

  const filters: { label: string; value: Category }[] = [
    { label: 'All', value: 'all' },
    { label: 'App', value: 'app' },
    { label: 'Card', value: 'card' },
    { label: 'Web', value: 'web' },
  ];

  return (
    <section id="portfolio" className="bg-background">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-primary mb-2">Portfolio</h2>
          <div className="inline-block w-16 h-1 bg-accent mb-4"></div>
          <p className="text-muted-foreground max-w-3xl mx-auto">
            Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.
          </p>
        </div>

        {/* Filter Buttons */}
        <div className="flex justify-center mb-12 space-x-2">
          {filters.map(filter => (
            <Button
              key={filter.value}
              variant={activeFilter === filter.value ? 'default' : 'outline'}
              onClick={() => setActiveFilter(filter.value)}
              className={`rounded-full px-4 py-1 h-auto text-sm font-medium transition-colors duration-200 ${activeFilter === filter.value ? 'bg-accent text-accent-foreground border-accent hover:bg-accent-dark' : 'text-primary border-border hover:bg-accent/10 hover:text-accent hover:border-accent/50'}`}
            >
              {filter.label}
            </Button>
          ))}
        </div>

        {/* Portfolio Grid */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
          {filteredItems.map((item) => (
             <Card key={item.id} className="group overflow-hidden rounded-lg border-none shadow-sm relative">
                <CardContent className="p-0">
                 <Image
                   src={item.imageUrl}
                   alt={item.title}
                   width={600}
                   height={400}
                   className="w-full h-auto object-cover transition-transform duration-300 group-hover:scale-105"
                 />
                 <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-60 transition-opacity duration-300 flex flex-col justify-end p-4 opacity-0 group-hover:opacity-100">
                    <div>
                         <h4 className="text-lg font-semibold text-white mb-1">{item.title}</h4>
                         <p className="text-sm text-gray-300 capitalize">{item.category}</p>
                    </div>
                   <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 flex space-x-2 opacity-0 group-hover:opacity-100 transition-opacity duration-300 delay-100">
                     <Button variant="outline" size="icon" className="bg-accent/80 hover:bg-accent border-none rounded-full text-white h-9 w-9">
                       <Plus size={20} />
                       <span className="sr-only">View Details</span>
                     </Button>
                     <Button asChild variant="outline" size="icon" className="bg-accent/80 hover:bg-accent border-none rounded-full text-white h-9 w-9">
                       <Link href="#">
                         <LinkIcon size={20} />
                         <span className="sr-only">Visit Link</span>
                       </Link>
                     </Button>
                   </div>
                 </div>
               </CardContent>
             </Card>
          ))}
        </div>
      </div>
    </section>
  );
};

export default PortfolioSection;
